/* src/app/pages/home/home.component.scss */

.banner {
  background: #f6bd38;
  border-radius: 32px;
  margin: 48px auto;
  max-width: 96vw;
  min-height: 420px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 24px rgba(0,0,0,0.07);
  overflow: hidden;
}

.banner-content {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.banner-row {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  position: relative;
  gap: 0;
}

.banner-left {
  flex: 1 1 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  z-index: 2;
  margin-top: 60px;
  margin-left: 0;
  margin-right: 0;
  max-width: 65%;
}

.banner h1 {
  color: #fff;
  font-size: 2.8rem;
  font-weight: 800;
  margin-bottom: 38px;
  line-height: 1.2;
  text-align: left;
  max-width: 95%;
  margin-right: 32px;
  word-break: break-word;
}

.search-bar {
  display: flex;
  justify-content: flex-start;
  width: 100%;
}

.input-group {
  display: flex;
  background: #fff;
  border-radius: 15px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  align-items: stretch;
  width: 700px;
  max-width: 5200px; /* Valor alto para não restringir aqui */
  min-width: 380px;
  margin: 0;
  padding: 0;
  height: 56px;
  margin-top: 0;
  justify-content: space-between;
  position: sticky;
  top: 24px;
  z-index: 50;
  box-shadow: 0 2px 12px rgba(0,0,0,0.10);
  background: #fff;
  margin-bottom: 24px;
  gap: 8px;
}

.custom-field {
  flex: 1 1 0;
  min-width: 120px;
  max-width: 220px;
  margin-right: 8px;
  background: none;
  border: none;
  box-shadow: none;
  padding: 0;
  display: flex;
  align-items: center;
  border-radius: 0;
  height: 100%;
}
.custom-field:last-child {
  margin-right: 0;
}

.mat-form-field {
  width: 100%;
  background: none;
}

.mat-form-field-appearance-outline .mat-form-field-outline {
  color: #eee;
}

.city-input, .long-query {
  min-width: 120px;
  max-width: 220px;
  font-style: italic;
}

.icon {
  font-size: 1.3rem;
  color: #3B221B;
  margin-right: 6px;
}
.brown {
  color: #3B221B !important;
}
.brown-text {
  color: #3B221B !important;
}
::placeholder {
  color: #3B221B !important;
  opacity: 0.7;
  font-style: italic;
}

.search-btn {
  background: #f6bd38;
  color: #222;
  font-weight: 800;
  border: none;
  border-radius: 15px;
  padding: 0 22px;
  height: 44px;
  margin-left: 12px;
  margin-right: 4px;
  font-size: 1.05rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.10);
  transition: background 0.2s, color 0.2s;
  cursor: pointer;
  outline: none;
  min-width: 110px;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
}
.search-btn:hover, .search-btn:focus {
  background: #ffe08a;
  color: #222;
}

.city-dropdown-absolute {
  position: absolute;
  left: 50%;
  top: 100px;
  transform: translateX(-50%);
  width: 340px;
  z-index: 100;
  margin-left: 0;
}
.dropdown {
  background: #fff;
  box-shadow: 0 4px 16px rgba(0,0,0,0.10);
  border-radius: 0 0 14px 14px;
  z-index: 20;
  max-height: 260px;
  overflow-y: auto;
  margin-top: 2px;
}
.city-dropdown {
  padding: 12px 0 8px 0;
  min-width: 320px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.13);
  border-radius: 0 0 14px 14px;
}
.dropdown-city-search {
  width: 90%;
  margin: 0 5% 10px 5%;
  padding: 10px 12px;
  border-radius: 8px;
  border: 1px solid #eee;
  font-size: 1rem;
  color: #3B221B;
  background: #fafafa;
}
.dropdown-title {
  font-size: 0.98rem;
  font-weight: 600;
  color: #3B221B;
  padding: 8px 18px 4px 18px;
  opacity: 0.7;
}
.highlight {
  background: #f6f6f6;
  font-weight: 600;
  border-radius: 8px;
  margin: 0 8px 8px 8px;
  padding: 12px 18px !important;
  display: flex;
  align-items: center;
  gap: 8px;
}
.dropdown-item {
  padding: 14px 18px;
  cursor: pointer;
  color: #3B221B;
  font-size: 1rem;
  border-bottom: 1px solid #f6f6f6;
  transition: background 0.15s, color 0.15s;
  display: flex;
  align-items: center;
  gap: 8px;
  border-radius: 6px;
}
.dropdown-item:last-child {
  border-bottom: none;
}
.dropdown-item:hover {
  background: #f6bd38;
  color: #3B221B;
  font-weight: 700;
}

.banner-right {
  flex: 0 0 420px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  position: absolute;
  right: -18%;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  overflow: visible;
}
.pizza-img {
  max-width: 420px;
  max-height: 420px;
  width: 100%;
  height: auto;
  object-fit: contain;
  filter: drop-shadow(0 4px 16px rgba(0,0,0,0.10));
  overflow: visible;
}

@media (max-width: 1100px) {
  .banner-row { flex-direction: column; align-items: center; }
  .banner-right { position: static; right: 0; top: 0; transform: none; margin-top: 32px; justify-content: center; }
  .pizza-img { margin-left: 0; max-width: 220px; }
  .banner-left { margin-left: 0; margin-top: 32px; align-items: center; margin-right: 0; max-width: 100%; }
  .input-group { width: 98vw; max-width: 98vw; }
  .city-dropdown-absolute { position: static; margin-left: 0; width: 98vw; left: 0; transform: none; }
}
@media (max-width: 700px) {
  .banner-content { max-width: 98vw; }
  .banner-row { flex-direction: column; align-items: center; }
  .banner-left { align-items: center; margin-left: 0; margin-right: 0; max-width: 100%; }
  .input-group {
    flex-direction: column;
    gap: 8px;
  }
  .custom-field {
    min-width: 100%;
    max-width: 100%;
    margin-right: 0;
  }
  .search-btn { margin-top: 8px; height: 48px; }
  .pizza-img { max-width: 180px; }
  .banner h1 { font-size: 1.5rem; text-align: center; margin-right: 0; }
}

.results-grid {
  margin-top: 24px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 24px;
}
.rest-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
  padding: 16px;
  transition: transform .2s;
  cursor: pointer;
  &:hover { transform: scale(1.03); }
  img { width: 100%; border-radius: 8px; }
  .tipo { font-size: 13px; color: #DA4A24; font-weight: 600; margin-bottom: 4px; }
  .nome { font-size: 18px; font-weight: 700; margin-bottom: 8px; }
}

.restaurants-section {
  margin: 48px auto 0 auto;
  max-width: 1200px; /* Largura máxima padrão para 4 cards visíveis*/
  padding: 0 24px;
  transition: max-width 0.3s ease, padding 0.3s ease; /* Transição suave*/

  &.sidebar-aberta {
    /* Ajusta a largura máxima da seção quando a sidebar está aberta*/
    /* Exemplo: 1200px (largura original) - 240px (largura sidebar) - 2*16px (margens da sidebar) = 928px*/
    max-width: 928px; 
    padding: 0 16px; /* Ajuste de padding se necessário*/
    margin-left: auto;
    margin-right: auto;
  }
}

.restaurants-section h2 {
  font-size: 2rem;
  font-weight: 700;
  color: #3B221B;
  margin-bottom: 24px;
}

/* Remova o display: grid e grid-template-columns daqui */
