.restaurant-card {
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  transition: box-shadow 0.2s, transform 0.2s;
  cursor: pointer;
  &:hover {
    box-shadow: 0 8px 24px rgba(0,0,0,0.13);
    transform: translateY(-2px) scale(1.01);
  }
}
.restaurant-img {
  width: 100%;
  height: 160px;
  object-fit: cover;
  border-radius: 18px 18px 0 0;
}
.restaurant-info {
  padding: 18px 18px 14px 18px;
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.restaurant-type {
  font-size: 0.95rem;
  color: #DA4A24;
  font-weight: 600;
  margin-bottom: 2px;
}
.restaurant-name {
  font-size: 1.18rem;
  font-weight: 700;
  color: #3B221B;
  margin-bottom: 6px;
}
.restaurant-rating {
  display: flex;
  align-items: center;
  gap: 4px;
}
.star {
  font-size: 1.1rem;
  color: #e0e0e0;
}
.star.filled {
  color: #F6BD38;
}
.rate-number {
  font-size: 1rem;
  font-weight: 600;
  color: #3B221B;
  margin-left: 6px;
}

/* Styles for restaurant address */
.restaurant-address {
  font-size: 0.9rem;
  color: #555;
  margin-bottom: 4px;
}

.see-more-container {
  text-align: center;
  margin-top: 16px;
}

.see-more-button {
  color: #DA4A24; /* Tavola red */
  mat-icon {
    font-size: 1.1rem;
  }
}

/* Styles for horizontal scrolling and navigation arrows */
.cuisine-group {
  margin-bottom: 32px;
}

.cuisine-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;

  h2 {
    font-size: 2rem;
    font-weight: 700;
    color: #3B221B;
    margin: 0;
  }

  .navigation-arrows {
    display: flex;
    gap: 8px;

    button {
      width: 32px; /* Fixed width and height for circular button*/
      height: 32px; /**/
      border-radius: 50%; /**/
      background-color: #fff; /* White background*/
      border: 1px solid #e0e0e0; /* Medium gray border*/
      color: #3B221B; /* Text color*/
      cursor: pointer;
      transition: all 0.2s ease; /* Smooth transitions*/
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: 0 2px 5px rgba(0,0,0,0.1); /* Subtle shadow*/

      &:hover:not(:disabled) {
        background-color: #f5f5f5; /* Light gray background on hover*/
        border-color: #f6bd38; /* Primary color border on hover*/
        color: #f6bd38; /* Primary color on hover*/
      }

      &:disabled {
        opacity: 0.4;
        cursor: not-allowed;
        box-shadow: none; /* No shadow when disabled*/
      }
      mat-icon {
        font-size: 14px; /* Icon size*/
        width: 14px;
        height: 14px;
      }
    }
  }
}

.scrollable-restaurants {
  overflow-x: auto; /* Permite a rolagem horizontal*/
  padding-bottom: 20px;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none; /* Oculta a scrollbar no Firefox*/
  -ms-overflow-style: none;  /* Oculta a scrollbar no IE/Edge*/
  scroll-behavior: smooth; /* Rolagem suave*/

  &::-webkit-scrollbar {
    display: none; /* Oculta a scrollbar no Chrome/Safari*/
  }

  .restaurants-grid {
    display: flex; /* Define como flex container*/
    flex-wrap: nowrap; /* Impede a quebra de linha, mantendo os itens em uma única linha*/
    gap: 24px; /* Espaçamento entre os cards*/

    .restaurant-card {
      /* Padrão: 4 cards por linha visíveis quando a sidebar está FECHADA*/
      /* Calcula a largura para 4 cards, subtraindo o espaço dos gaps*/
      flex: 0 0 calc((100% - (3 * 24px)) / 4);
      max-width: calc((100% - (3 * 24px)) / 4);
      min-width: 260px; /* Garante um tamanho mínimo para o card*/
    }
  }
}

/* Ajusta a largura dos cards quando a sidebar está ABERTA para exibir 3 cards por linha*/
.restaurants-section.sidebar-aberta .scrollable-restaurants .restaurants-grid .restaurant-card {
  /* Calcula a largura para 3 cards, subtraindo o espaço dos gaps*/
  flex: 0 0 calc((100% - (2 * 24px)) / 3);
  max-width: calc((100% - (2 * 24px)) / 3);
  min-width: 250px; /* Min-width para 3 cards no espaço reduzido*/
}

@media (max-width: 900px) {
  .restaurants-section {
    padding: 0 8px;

    &.sidebar-aberta {
      padding: 0 8px;
    }
  }
  .scrollable-restaurants .restaurants-grid {
    gap: 18px; /* Reduz o gap para telas menores*/

    .restaurant-card {
      /* Em telas menores (ex: tablets), pode ser 2 cards por linha*/
      flex: 0 0 calc((100% - (1 * 18px)) / 2); /* 2 cards com 1 gap de 18px*/
      max-width: calc((100% - (1 * 18px)) / 2);
      min-width: 200px;
    }
  }
  .restaurants-section.sidebar-aberta .scrollable-restaurants .restaurants-grid .restaurant-card {
    /* Em telas menores, quando a sidebar está aberta, mantém 2 cards*/
    flex: 0 0 calc((100% - (1 * 18px)) / 2);
    max-width: calc((100% - (1 * 18px)) / 2);
  }
}

@media (max-width: 700px) {
  .scrollable-restaurants .restaurants-grid .restaurant-card {
    /* Em telas de celular, 1 card por linha*/
    flex: 0 0 100%;
    max-width: 100%;
  }
  .restaurants-section.sidebar-aberta .scrollable-restaurants .restaurants-grid .restaurant-card {
    /* Em telas de celular, quando a sidebar está aberta, ainda 1 card*/
    flex: 0 0 100%;
    max-width: 100%;
  }
}